.page-body {
  background-color: #264F55;
  font-family: "Montserrat";
  font-size: 18px;
  line-height: 22px;
  font-weight: 500;
  font-style: normal;
  color: #FFFFFF;
}
.page__body {
  margin: 0;
  padding: 0;
  height: 100%;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
}
.page-header {
  background: url("../img/bg/header-bg.jpg") no-repeat center;
  min-height: 1081px;
}
.page-header__wrapper {
  max-width: 1600px;
  margin: 0 auto;
}
.page-header__top {
  padding-top: 23px;
  display: flex;
  justify-content: space-between;
}
.page-heading {
  font-size: 140px;
  line-height: 171px;
  font-weight: 600;
}
.page-heading span {
  font-size: 296px;
  line-height: 296px;
}
.page__heading {
  display: block;
  width: 940px;
  margin-top: 105px;
  text-align: right;
}
.page__heading span {
  display: block;
  margin-left: -20px;
  padding: 0;
  text-align: left;
}
.logo {
  font-size: 17px;
  line-height: 21px;
  font-weight: 400;
}
.logo-link {
  color: #FFFFFF;
  text-decoration: none;
}
.logo__grid {
  display: grid;
  grid-template-columns: min-content 200px;
  column-gap: 29px;
}
.logo__image {
  display: block;
  grid-row-start: 1;
  grid-row-end: 3;
}
.logo p {
  margin: 0;
  padding: 0;
}
.header__links {
  display: grid;
  grid-template-columns: 423px 144px;
  row-gap: 22px;
}
.contacts {
  margin: 0;
  padding: 0;
  justify-self: flex-end;
  list-style: none;
  display: flex;
}
.contacts__item:not(:last-child) {
  margin-right: 14px;
}
.contacts__link {
  display: block;
  margin: 0;
  padding: 0;
}
.language {
  justify-self: flex-end;
}
.language-toggle {
  display: inline-block;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
}
.toggle-image {
  display: block;
  margin: 0;
  padding: 0;
  width: 24px;
  height: 24px;
  border-radius: 50%;
}
.toggle-arabian {
  background: url("../img/logos/arabian.svg") no-repeat center;
}
.toggle-arabian--active {
  background: url("../img/logos/arabian-active.svg") no-repeat center;
}
.toggle-english {
  background: url("../img/logos/english.png") no-repeat center;
}
.toggle-english--active {
  background: url("../img/logos/english-active.png") no-repeat center;
}
.nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.nav__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}
.nav-list a {
  color: #FFFFFF;
  text-decoration: none;
}
.nav__list a {
  margin-right: 10px;
}
.nav-item:last-child a {
  margin: 0;
}
.main__wrapper {
  background: url("../img/bg/pattern.png");
}
.search__wrapper {
  max-width: 1600px;
  margin: 0 auto;
}
.search-form {
  font-size: 24px;
  line-height: 29px;
}
.search__form {
  display: flex;
  justify-content: space-between;
  padding-top: 26px;
}
.search-label {
  display: flex;
  flex-direction: column;
}
.search-select,
.search-input {
  height: 72px;
  box-sizing: border-box;
  margin-top: 20px;
  padding: 19px 23px 23px 23px;
  background-color: #264F55;
  border: 3px solid #FFFFFF;
  font-size: 24px;
  line-height: 29px;
  font-weight: 500;
  color: #FFFFFF;
}
.select-city {
  width: 500px;
}
.select-number {
  width: 287px;
}
.search-input {
  width: 424px;
}
.search-submit {
  width: 304px;
  height: 72px;
  box-sizing: border-box;
  padding: 19px 23px 23px 23px;
  align-self: flex-end;
  font-size: 24px;
  line-height: 29px;
  font-weight: 500;
}
.intro__wrapper {
  max-width: 1600px;
  margin: 0 auto;
  margin-top: 60px;
}
.intro {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.intro-heading {
  font-size: 60px;
  line-height: 73px;
  font-weight: 400;
  width: 670px;
}
.video-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.video-item {
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 7px 7px 10px rgba(0, 0, 0, 0.35);
}
.video-item img {
  display: block;
}
.video-toggles {
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 85px;
}
.slider-toggle {
  display: block;
  margin: 0;
  padding: 0;
  width: 17px;
  height: 17px;
  border-radius: 50%;
}
.slider-toggle:not(:last-child) {
  margin-right: 30px;
}
